import React, { useEffect, useState } from 'react';
import './Contact.css';

function Contact() {
    const[message,setMeaage]= useState("");
    const[count,setCount]= useState(0);

    const [formData, setFormData] = useState({
        name: '',
        email: '',
        message: ''
    });

    const handleChange = (e) => {
        const { name, value } = e.target;
        setFormData({
            ...formData,
            [name]: value
        });
    };

    const handleSubmit = (e) => {
        e.preventDefault();
        // Handle form submission logic here
        console.log(formData);
        // Reset form fields
        setFormData({
            name: '',
            email: '',
            message: ''
        });
    };

    const handleButtonClick=(buttonName)=>{
       
        setCount(count+1);
        if(buttonName=="Button 1") {
            setMeaage("HELLI THIS IS MESSAGE ")
        }
        else if(buttonName=="Button 2") {
            setMeaage("HELLI THIS IS MESSAGE of Button 2")
        }
        return(
           <h1>Hamza</h1>
        );
       
    }
    useEffect(()=>{
       
    },[count])

    
    return (
        <div className="contact-container">
            <h1>Contact Us</h1>
            <form onSubmit={handleSubmit} className="contact-form">
                <label>
                    Name:
                    <input
                        type="text"
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                        required
                    />
                </label>
                <label>
                    Email:
                    <input
                        type="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        required
                    />
                </label>
                <label>
                    Message:
                    <textarea
                        name="message"
                        value={formData.message}
                        onChange={handleChange}
                        required
                    ></textarea>
                </label>
                <button type="submit">Submit</button>
            </form>
            <div className="button-group">
                    <button type="button" onClick={() => handleButtonClick('Button 1')}>Button 1</button>
                    <button type="button" onClick={() => handleButtonClick('Button 2')}>Button 2</button>
                    <button type="button" onClick={() => handleButtonClick('Button 3')}>Button 3</button>
                    <button type="button" onClick={() => handleButtonClick('Button 4')}>Button 4</button>
                    <button type="button" onClick={() => handleButtonClick('Button 5')}>Button 5</button>
                </div>
            <p>{message}</p>
            <p>{count}</p>
        </div>
    );
}

export default Contact;
