import React, { useState } from 'react';
import './Contact.css';
import { Routes, Route, Link, useLocation, useNavigate } from 'react-router-dom';
import logo from '../logo.svg';
import '../App.css';
import Homepage from './Homepage';
import Projects from './Projects';
import Calculator from './Calculator';
import CalculatorAdvance from './calculator/Calculator';
import Todo from './todo-app/Todo';
import TypingApp from './typing-test/TypingTest';
import burgericon from '../assets/mobile-menu.svg';
import Contact from './Contact';
import './Header.css';
import Switch from '../Switch';
import { useTheme } from '../ThemeContext';
import { useSpinner } from './SpinnerContext';

const Header = () => {
    const [active, setActive] = useState("nav-menu2");
    const location = useLocation();
    const navigate = useNavigate();
    const { theme } = useTheme();
    const { showSpinner, hideSpinner, loading } = useSpinner();

    const handleClick = (path) => {
        setActive(active === "nav-menu2" ? "nav-menu2 active" : "nav-menu2");
        showSpinner();
        setTimeout(() => {
            navigate(path);
            hideSpinner();
        }, 600);
    };

    const handleClickwithoutloader = () => {
        setActive(active === "nav-menu2" ? "nav-menu2 active" : "nav-menu2");
    };

    const pathParts = location.pathname.split('/');
    const lastPart = pathParts[pathParts.length - 1] || 'home';

    // Define the pages where the header should not be shown
    const hideHeaderAndBackground = [
        'calculator-advance',
        'typing-test',
        'todo-list-app',
    ].includes(lastPart);

    return (
        <div className={`body ${lastPart}`} data-theme={theme}>
            {!hideHeaderAndBackground && (
                <div className={`content ${loading ? 'hidden' : ''}`}> {/* Hide content when loading */}
                    <div className="header">
                        <div className='logo'>
                            <img src={logo} className="App-logo" alt="logo" onClick={() => { handleClick('/portfolio/') }} />
                            <p className='header-name'>Hamza</p>
                            <Switch />
                        </div>
                        <nav className='nav'>
                            <Link to="/portfolio/" className={`nav-item ${location.pathname === '/portfolio/' ? 'active' : ''}`} onClick={() => handleClick('/portfolio/')}>HOME</Link>
                            <Link to="/portfolio/projects" className={`nav-item ${location.pathname === '/portfolio/projects' || location.pathname === '/portfolio/projects/calculator' ? 'active' : ''}`} onClick={() => handleClick('/portfolio/projects')}>PROJECTS</Link>
                            <a href="https://github.com/hamzasyed2985" className="nav-item">GITHUB</a>
                            <Link to="/portfolio/contact" className={`nav-item ${location.pathname === '/portfolio/contact' ? 'active' : ''}`} onClick={() => handleClick('/portfolio/contact')}>Contact</Link>
                        </nav>
                    </div>
                    <div className="mobile-header">
                        <div className='logo'>
                            <Link to="/portfolio/"><img src={logo} className="App-logo" alt="logo" /></Link>
                            <p className='header-name'>Hamza</p>
                            <Switch />
                        </div>
                        <div className="burger-menu">
                            <button id="burger-btn" onClick={() => handleClickwithoutloader()}>
                                <img src={burgericon} alt="mobile-menu" />
                            </button>
                        </div>
                        <ul className={active}>
                            <li>
                                <Link to="/portfolio/" className={`nav-item ${location.pathname === '/portfolio/' ? 'active' : ''}`} onClick={() => handleClick('/portfolio/')}>HOME</Link>
                                <hr className="hr" />
                            </li>
                            <li>
                                <Link to="/portfolio/projects" className={`nav-item ${location.pathname === '/portfolio/projects' || location.pathname === '/portfolio/projects/calculator' ? 'active' : ''}`} onClick={() => handleClick('/portfolio/projects')}>PROJECTS</Link>
                                <hr className="hr" />
                            </li>
                            <li>
                                <a href="https://github.com/hamzasyed2985" className="nav-item" onClick={() => handleClick(location.pathname)}>GITHUB</a>
                                <hr className="hr" />
                            </li>
                            <li>
                                <Link to="/portfolio/contact" className={`nav-item ${location.pathname === '/portfolio/contact' ? 'active' : ''}`} onClick={() => handleClick('/portfolio/contact')}>Contact</Link>
                                <hr className="hr" />
                            </li>
                        </ul>
                    </div>
                    <Routes>
                <Route path="/portfolio/" element={<Homepage />} />
                <Route path="/portfolio/projects" element={<Projects />} />
                <Route path="/portfolio/contact" element={<Contact />} />
                <Route path="/portfolio/projects/calculator" element={<Calculator />} />
                <Route path="/portfolio/projects/calculator-advance" element={<CalculatorAdvance />} />
                <Route path="/portfolio/projects/todo-list-app" element={<Todo />} />
                <Route path="/portfolio/projects/typing-test" element={<TypingApp />} />
            </Routes>
                </div>
            )}
            <Routes>
             
                <Route path="/portfolio/projects/calculator-advance" element={<CalculatorAdvance />} />
                <Route path="/portfolio/projects/todo-list-app" element={<Todo />} />
                <Route path="/portfolio/projects/typing-test" element={<TypingApp />} />
            </Routes>
        </div>
    );
}

export default Header;
