import logo from '../logo.svg';
import React from 'react';
import {useState} from "react"; 
import "./Calculator.css";



function Calculator() {
  const inputRef = useState(null); 
  const resultRef = useState(null); 
  const [result, setResult] = useState(0); 
  const [inputText, setText] = useState(''); 
  
  
  function handleChange(e) { 
    setText(e.target.value); 
  } 

  function plus(event) { 
    event.preventDefault(); 
    setResult((result) => result + Number(inputRef.current.value)); 
  }; 
 
  function minus(e) {
    e.preventDefault();
    setResult((result) => result - Number(inputRef.current.value));
  };
 
  function times(e) { 
    e.preventDefault();
    setResult((result) => result * Number(inputRef.current.value));
  }; 
 
  function divide(e) { 
    e.preventDefault();
    setResult((result) => result / Number(inputRef.current.value));
  };
 
  function resetInput(e) {
    e.preventDefault();
    inputRef.current.value=null; 
  }; 
 
  function resetResult(e) { 
    e.preventDefault();
    setResult(() => 0);
  }; 
  
  
  return ( 
    <div className="main-calculator">

      <div className='calculator-header'>
      <img src={logo} className="App-logo" alt="logo" /> 
        <h1>Simplest Working Calculator</h1> 
      </div> 
      <form> 
        <div>
        <p ref={resultRef} >Result =  
          {result} 
        </p>
        <p>You typed: {inputText}</p>  
        </div>
        <input
        onChange={handleChange}
          pattern="[0-9]" 
          ref={inputRef} 
          type="number" 
          placeholder="Type a number" 
        />
        <div className='button-class'> 
        <button className='calculator-button' onClick={plus}>Add</button> 
        <button className='calculator-button' onClick={minus}>Minus</button> 
        <button className='calculator-button' onClick={times}>Times</button> 
        <button className='calculator-button' onClick={divide}>Divide</button> 
        <button className='calculator-button' onClick={resetInput}>Reset Input</button> 
        <button className='calculator-button' onClick={resetResult}>Reset Result</button> 
        </div>
      </form> 
    </div> 
  ); 
}

export default Calculator;

