import React, { useState } from 'react';
import './Todo.css';
import { motion, AnimatePresence } from 'framer-motion';
import styled, { keyframes } from 'styled-components';
import { fadeIn } from 'react-animations';

const fadeInAnimation = keyframes`${fadeIn}`;
const FadeIn = styled.div`
  animation: 0.5s ${fadeInAnimation};
`;

let count = 1;

const Todo = () => {
    const [tasks, setTasks] = useState([]);
    const [editingTaskId, setEditingTaskId] = useState(null);
    const [editTaskText, setEditTaskText] = useState("");
    const [filterValue, setFilterValue] = useState('All');

    const handleAddTask = (e) => {
        e.preventDefault();
        const newTaskText = e.target.elements.newTask.value;
        if (newTaskText) {
            setTasks([
                ...tasks,
                { id: count += 1, text: newTaskText, completed: false },
            ]);
            e.target.elements.newTask.value = "";
        }
    };

    const handleSelectChange = (event) => {
        setFilterValue(event.target.value);
    };

    const handleToggle = (taskID) => {
        setTasks(tasks.map((t) =>
            t.id === taskID ? { ...t, completed: !t.completed } : t
        ));
    };

    const clearCompleted = () => {
        setTasks(tasks.filter((t) => !t.completed));
    };

    const startEditing = (taskID, currentText) => {
        setEditingTaskId(taskID);
        setEditTaskText(currentText);
    };

    const handleEditTask = (e) => {
        e.preventDefault();
        setTasks(tasks.map((t) =>
            t.id === editingTaskId ? { ...t, text: editTaskText } : t
        ));
        setEditingTaskId(null);
        setEditTaskText("");
    };

    const handleDeleteTask = (taskID) => {
        setTasks(tasks.filter((t) => t.id !== taskID));
    };

    const filteredTasks = tasks.filter((task) =>
        filterValue === 'All' || (filterValue === 'Completed' && task.completed)
    );

    return (
        <div className='todo-main'>
            <div className='todo-header'>
            <h1>Todo App</h1>
            </div>
        <div className="todo-app">
            <form onSubmit={handleAddTask}>
                <input type="text" id='task' name="newTask" placeholder="What do you need to do?" />
                <button className='submit-button' type="submit">ADD TASK</button>
            </form>

            <div className='filter-div'>
                <select name="filter" id="filter" value={filterValue} onChange={handleSelectChange}>
                    <option value="All">All</option>
                    <option value="Completed">Completed</option>
                </select>
            </div>

            <div className='tasks-title'>
                <span>Tasks</span>
                <span>Edit / Delete</span>
            </div>

            <ul>
                <AnimatePresence>
                    {filteredTasks.map((task) => (
                        <motion.li
                            key={task.id}
                            initial={{ opacity: 0, y: -20 }}
                            animate={{ opacity: 1, y: 0 }}
                            exit={{ opacity: 0, y: 10 }}
                            transition={{ duration: 0.5 }}
                        >
                            <div className="checkbox-wrapper-15">
                                <input className="inp-cbx" id={`cbx-${task.id}`} type="checkbox" checked={task.completed} onChange={() => handleToggle(task.id)} style={{ display: 'none' }} />
                                <label className="cbx" htmlFor={`cbx-${task.id}`}>
                                    <span>
                                        <svg width="12px" height="9px" viewBox="0 0 12 9">
                                            <polyline points="1 5 4 8 11 1"></polyline>
                                        </svg>
                                    </span>
                                    <span> {task.text}</span>
                                </label>
                            </div>
                            <div>
                                <button id='edit-button' onClick={() => startEditing(task.id, task.text)}>Edit</button>
                                <button id='delete-button' onClick={() => handleDeleteTask(task.id)}>🗑️</button>
                            </div>
                        </motion.li>
                    ))}
                </AnimatePresence>
            </ul>

            {editingTaskId && (
                <FadeIn>
                    <div className="modal">
                        <form onSubmit={handleEditTask}>
                            <input
                                type="text"
                                name="editTask"
                                value={editTaskText}
                                onChange={(e) => setEditTaskText(e.target.value)}
                                required
                            />
                            <button type="submit">Save</button>
                        </form>
                    </div>
                </FadeIn>
            )}

            <button className="clear-completed" onClick={clearCompleted}>
                Clear Completed
            </button>
        </div>
        </div>
    );
};

export default Todo;
