import React from "react";
import { Heading } from "@chakra-ui/react";
import './Card.css';
import { useNavigate } from 'react-router-dom';
import { useSpinner } from './SpinnerContext';

const Card = ({ title, description, imageSrc, target }) => {
  const { showSpinner, hideSpinner } = useSpinner();
  const navigate = useNavigate();

  const handleClick = (path, isNewTab) => {
    showSpinner();
    setTimeout(() => {
      if (isNewTab) {
        window.open(path, '_blank');
      } else {
        navigate(path);
      }
      hideSpinner();
    }, 600);
  };

  const isNewTab = 
    title === "Typing Test App" ||
    title === "Advance Calculator" ||
    title === "Todo List App" ||
    title === "Simple React Calculator";

  return (
    <div className="card">
      <div className="image-div">
        <img src={imageSrc} alt={title} style={{ width: "100%" }} />
      </div>
      <Heading size="md" color="teal.500">
        {title}
      </Heading>
      {isNewTab ? (
        <button className="button-64" onClick={() => handleClick(target, true)}>
          <span className="text">Learn More</span>
        </button>
      ) : (
        <a href={target} target="_blank" rel="noopener noreferrer" className="learn-more">
          <button className="button-64">
            <span className="text">Learn More</span>
          </button>
        </a>
      )}
    </div>
  );
};

export default Card;
