import React, { useState } from 'react';
import './Calculator.css';


const Calculator = () => {
  const [result, setResult] = useState(" ");
  const [expression, setExpression] = useState(" ");
  const [lastAnswer, setLastAnswer] = useState(null);
  const [evaluated, setEvaluated] = useState(false);

  const handleChange = (value) => {
    if (evaluated) {
      setResult(value);
      setEvaluated(false);
    } else {
      setResult(result + value);
    }
  };

  const handleClear = () => {
    setExpression(" ");
    setResult(" ");
    setLastAnswer(null);
    setEvaluated(false);
  };

  const handleDelete = () => {
    setResult(result.slice(0, result.length - 1));
  };

  const handleAns = () => {
    if (lastAnswer !== null) {
      setResult(result + lastAnswer.toString());
    }
  };

  const evaluate = () => {
    try {
      const sanitizedExpression = result.replace(/(^|[^\d.])0+(\d)/g, '$1$2');
      const evalResult = eval(sanitizedExpression);
      setExpression(result);
      setResult(`${evalResult}`);
      setLastAnswer(evalResult);
      setEvaluated(true); // Set evaluated to true
    } catch (error) {
      setResult('Error');
    }
  };

  return (
    <div className="calculator">
      <div className="calculator-display">
        <span className="calculator-expression">{expression}</span>
        <span className="calculator-result">{result}</span>
      </div>
      <div className="calculator-buttons">
        <button onClick={() => handleChange('(')}>(</button>
        <button onClick={() => handleChange(')')}>)</button>
        <button onClick={handleAns}>Ans</button>
        <button onClick={handleDelete}>DEL</button>
        <button onClick={() => handleChange('%')}>%</button>
        <button onClick={() => handleChange('√')}>√</button>
        <button onClick={() => handleChange('*')}>x</button>
        <button onClick={() => handleChange('/')}>÷</button>
        <button onClick={() => handleChange('7')}>7</button>
        <button onClick={() => handleChange('8')}>8</button>
        <button onClick={() => handleChange('9')}>9</button>
        <button onClick={() => handleChange('-')}>-</button>
        <button onClick={() => handleChange('4')}>4</button>
        <button onClick={() => handleChange('5')}>5</button>
        <button onClick={() => handleChange('6')}>6</button>
        <button className='plus' onClick={() => handleChange('+')}>+</button>
        <button onClick={() => handleChange('1')}>1</button>
        <button onClick={() => handleChange('2')}>2</button>
        <button onClick={() => handleChange('3')}>3</button>
        <button className="enter" onClick={evaluate}>=</button>
        <button onClick={() => handleChange('0')}>0</button>
        <button onClick={() => handleChange('.')}>.</button>
        <button onClick={handleClear}>AC</button>
      </div>
    </div>
  );
};

export default Calculator;
